'use client';

import { useState } from 'react';
import { Tabs } from '@/components/common/Tabs';
import { motion } from 'framer-motion';

// Import all section components for preview
import { BannerStrip } from '@/components/common/BannerStrip';
import { Navigation } from '@/components/common/Navigation';
import { HeroSection } from '@/components/sections/HeroSection';
import { Value } from '@/components/sections/Value';
import { HowItWorks } from '@/components/sections/HowItWorks';
import { TestimonialSection } from '@/components/sections/TestimonialSection';
import { More } from '@/components/sections/More';
import { TickerSection } from '@/components/sections/TickerSection';
import { CTASection } from '@/components/sections/CTASection';
import { FAQSection } from '@/components/sections/FAQSection';
import { Footer } from '@/components/common/Footer';

interface Page {
  id: string;
  name: string;
  description: string;
  sections: string[];
}

const pages: Page[] = [
  {
    id: 'landing-page',
    name: 'Landing Page',
    description: 'Main homepage with all sections',
    sections: [
      'Banner',
      'Navigation', 
      'Hero',
      'Value',
      'How It Works',
      'Testimonials',
      'More',
      'Ticker',
      'CTA',
      'FAQ',
      'Footer'
    ]
  }
];

const sectionComponents = {
  'Banner': BannerStrip,
  'Navigation': Navigation,
  'Hero': HeroSection,
  'Value': Value,
  'How It Works': HowItWorks,
  'Testimonials': TestimonialSection,
  'More': More,
  'Ticker': TickerSection,
  'CTA': CTASection,
  'FAQ': FAQSection,
  'Footer': Footer
};

export default function AdminPage() {
  const [selectedPage, setSelectedPage] = useState<string | null>(null);
  const [activeSection, setActiveSection] = useState<string>('Banner');
  const [isPreviewMode, setIsPreviewMode] = useState<boolean>(false);

  const handlePageSelect = (pageId: string) => {
    setSelectedPage(pageId);
    setActiveSection('Banner'); // Reset to first section
  };

  const handleBackToPages = () => {
    setSelectedPage(null);
  };

  const renderEditableFields = (section: string) => {
    const commonFields = (
      <div className="space-y-6">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">Title</label>
          <input
            type="text"
            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#2C9B47] focus:border-transparent"
            placeholder={`Enter ${section} title`}
          />
        </div>
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">Subtitle</label>
          <textarea
            rows={3}
            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#2C9B47] focus:border-transparent"
            placeholder={`Enter ${section} subtitle`}
          />
        </div>
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">Description</label>
          <textarea
            rows={4}
            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#2C9B47] focus:border-transparent"
            placeholder={`Enter ${section} description`}
          />
        </div>
      </div>
    );

    switch (section) {
      case 'Banner':
        return (
          <div className="space-y-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Message</label>
              <input
                type="text"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#2C9B47] focus:border-transparent"
                placeholder="Enter banner message"
              />
            </div>
            <div className="flex items-center">
              <input type="checkbox" id="showCloseButton" className="mr-2" />
              <label htmlFor="showCloseButton" className="text-sm text-gray-700">Show close button</label>
            </div>
          </div>
        );
      case 'Hero':
        return (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {/* Left side - Editable Fields */}
            <div className="space-y-6">
              {commonFields}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Primary CTA Text</label>
                <input
                  type="text"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#2C9B47] focus:border-transparent"
                  placeholder="Get Started Now"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Secondary CTA Text</label>
                <input
                  type="text"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#2C9B47] focus:border-transparent"
                  placeholder="Learn More"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Hero Image</label>
                <input
                  type="file"
                  accept="image/*"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#2C9B47] focus:border-transparent"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Hero Video</label>
                <input
                  type="file"
                  accept="video/*"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#2C9B47] focus:border-transparent"
                />
              </div>
            </div>
            
            {/* Right side - Preview */}
            <div className="lg:sticky lg:top-8">
              <div className="bg-gray-50 rounded-lg p-4">
                <h4 className="text-sm font-medium text-gray-700 mb-3">Preview</h4>
                <div className="w-full h-48 bg-white rounded-lg border border-gray-200 flex items-center justify-center">
                  <div className="text-center">
                    <div className="w-8 h-8 bg-[#2C9B47]/20 rounded-full flex items-center justify-center mx-auto mb-2">
                      <svg className="w-4 h-4 text-[#2C9B47]" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                      </svg>
                    </div>
                    <p className="text-[#2C9B47] text-sm font-medium">Hero Preview</p>
                    <p className="text-[#2C9B47]/60 text-xs">Upload image/video to see preview</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );
      default:
        return commonFields;
    }
  };

  // If no page is selected, show page selection
  if (!selectedPage) {
    return (
      <div className="min-h-screen bg-[#FAFAFA] p-8">
        <div className="max-w-6xl mx-auto">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="mb-8"
          >
            <h1 className="text-4xl font-bold text-gray-900 mb-4">Content Management</h1>
            <p className="text-xl text-gray-600">Select a page to manage its content</p>
          </motion.div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {pages.map((page) => (
              <motion.div
                key={page.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                onClick={() => handlePageSelect(page.id)}
                className="bg-white rounded-2xl p-6 shadow-lg border border-gray-200 cursor-pointer hover:shadow-xl transition-all duration-200"
              >
                <h3 className="text-2xl font-bold text-gray-900 mb-3">{page.name}</h3>
                <p className="text-gray-600 mb-4">{page.description}</p>
                <div className="flex flex-wrap gap-2">
                  {page.sections.map((section) => (
                    <span
                      key={section}
                      className="px-3 py-1 bg-[#2C9B47]/10 text-[#2C9B47] text-sm rounded-full"
                    >
                      {section}
                    </span>
                  ))}
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  // If page is selected, show section management
  const currentPage = pages.find(p => p.id === selectedPage);
  const sectionTabs = currentPage?.sections.map(section => ({
    id: section,
    label: section
  })) || [];

  const ActiveComponent = sectionComponents[activeSection as keyof typeof sectionComponents];

  return (
    <div className="min-h-screen bg-[#FAFAFA]">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-8 py-4">
        <div className="max-w-6xl mx-auto flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <button
              onClick={handleBackToPages}
              className="text-gray-600 hover:text-[#2C9B47] transition-colors duration-200"
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
            </button>
            <div>
              <h1 className="text-2xl font-bold text-gray-900">{currentPage?.name}</h1>
              <p className="text-gray-600">Manage section content</p>
            </div>
          </div>
          <div className="flex items-center space-x-4">
            <button 
              onClick={() => setIsPreviewMode(!isPreviewMode)}
              className={`px-4 py-2 rounded-lg transition-colors duration-200 ${
                isPreviewMode 
                  ? 'bg-[#2C9B47] text-white' 
                  : 'border border-gray-300 text-gray-700 hover:bg-gray-50'
              }`}
            >
              {isPreviewMode ? 'Edit Mode' : 'Preview'}
            </button>
            <button className="px-4 py-2 bg-[#2C9B47] text-white rounded-lg hover:bg-[#2C9B47]/90 transition-colors duration-200">
              Publish Changes
            </button>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="p-0">
        <div className="w-full">
          <Tabs
            tabs={sectionTabs}
            defaultTab={activeSection}
            onTabChange={setActiveSection}
          >
            {/* Section Content */}
            <div className="bg-white min-h-screen">
              {isPreviewMode ? (
                /* Preview Mode */
                <div className="p-0">
                  {ActiveComponent && (
                    <ActiveComponent />
                  )}
                </div>
              ) : (
                /* Edit Mode */
                <div className="p-8">
                  <div className="max-w-4xl mx-auto">
                    <div className="mb-8">
                      <h3 className="text-2xl font-bold text-gray-900 mb-2">{activeSection} Section</h3>
                      <p className="text-gray-600">Edit the {activeSection.toLowerCase()} section content</p>
                    </div>
                    
                    {/* Editable Fields based on section */}
                    {renderEditableFields(activeSection)}
                  </div>
                </div>
              )}
            </div>
          </Tabs>
        </div>
      </div>
    </div>
  );
}
